.row {
    margin-left: 20px;
    color: white
}

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_title {
    padding-top: 10px;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.poster {
    object-fit: contain;
    width: 100%;
    margin-right: 6px;
    max-height: 100px;
    transition: transform 600ms;
}

.poster:hover {
    transform: scale(1.1);
}

.large_poster {
    max-height: 220px;
    margin-right: 10px;
}

.large_poster:hover {
    transform: scale(1.12);
}
.banner {
    color: white;
    object-fit: contain;
    height: 600px;
}

.banner_content {
    margin-left: 30px;
    padding-top: 300px;
    height: 190px;
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description {
    width: 45rem;
    font-size: 0.8rem;
    line-height: 1.3;
    padding-top: 1rem;
    max-width: 360px;
    height: 80px;
}

.banner_button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.5vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.6);
    padding-bottom: 0.5rem;
}

.btn-play {
    background-color: #D81F25;
}

.banner_button:hover {
    color: #000;
    background-color: #fff;
    transition: all 0.2s;
}

.banner--fadebottom {
    height: 16.9rem;
    background-image: linear-gradient( 180deg, transparent, rgba(37, 37, 37, 0.61), #111)
}
.nav {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    display: flex;
    padding: 10px;
    z-index: 1;
    justify-content: space-between;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_black {
    background: #111;
}

.nav_logo {
    width: 80px;
    position: fixed;
    left: 30px;
    object-fit: contain;
}

#content {
    position: absolute;
    height: 30px;
    width: 200px;
    right: 0;
    top: 50px;
    transform: translate(-50%, -50%);
}

#content.on {
    -webkit-animation-name: in-out;
    animation-name: in-out;
    -webkit-animation-duration: 0.7s;
    animation-duration: 0.7s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

input {
    box-sizing: border-box;
    width: 20px;
    height: 20px;
    border: 4px solid #ffffff;
    border-radius: 50%;
    background: none;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    font-family: Roboto;
    outline: 0;
    transition: width 0.4s ease-in-out, border-radius 0.8s ease-in-out, padding 0.2s;
    transition-delay: 0.4s;
    transform: translate(-100%, -50%);
}

.search {
    background: none;
    position: absolute;
    top: 0px;
    left: 0;
    height: 20px;
    width: 20px;
    padding: 0;
    border-radius: 100%;
    outline: 0;
    border: 0;
    color: inherit;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    transform: translate(-100%, -50%);
}

.search:before {
    content: "";
    position: absolute;
    width: 10px;
    height: 4px;
    background-color: #fff;
    transform: rotate(45deg);
    margin-top: 11px;
    margin-left: 2px;
    transition: 0.2s ease-in-out;
}

.close {
    transition: 0.4s ease-in-out;
    transition-delay: 0.4s;
}

.close:before {
    content: "";
    position: absolute;
    width: 27px;
    height: 4px;
    margin-top: -1px;
    margin-left: -13px;
    background-color: #fff;
    transform: rotate(45deg);
    transition: 0.2s ease-in-out;
}

.close:after {
    content: "";
    position: absolute;
    width: 27px;
    height: 4px;
    background-color: #fff;
    margin-top: -1px;
    margin-left: -13px;
    cursor: pointer;
    transform: rotate(-45deg);
}

.square {
    box-sizing: border-box;
    padding: 0 40px 0 10px;
    width: 300px;
    height: 50px;
    border: 4px solid #ffffff;
    border-radius: 0;
    background: none;
    color: #fff;
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    outline: 0;
    transition: width 0.4s ease-in-out, border-radius 0.4s ease-in-out, padding 0.2s;
    transition-delay: 0.4s, 0s, 0.4s;
    transform: translate(-100%, -50%);
}
* {
    margin: 0;
}

.Home {
    background-color: #111;
    padding: 0;
}

*::-webkit-scrollbar {
    display: none;
}
section {
    display: flex;
}

.movieDetail {
    color: whitesmoke;
    margin: 0;
}

.movie_info {
    margin-left: 20px;
}

* {
    font-family: 'Roboto', sans-serif;
    font-weight: normal;
}

.info_field {
    font-weight: 400;
    padding-bottom: 5px;
    padding-top: 15px;
    font-size: 0.8rem;
    color: #999;
}

@media(max-width: 768px) {
    section {
        flex-direction: column;
    }
    .movie-detail {
        flex-direction: column;
    }
}

main {
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
    width: 100%;
    background-color: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.yt {
    width: 100%;
    margin: 2rem;
}

.movie-detail {
    min-height: 80vh;
    width: 90%;
    margin-top: 2rem;
    background: linear-gradient( to right bottom, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.3));
    border-radius: 2rem;
    z-index: 2;
    -webkit-backdrop-filter: blur(1rem);
            backdrop-filter: blur(1rem);
    display: flex;
}

.movie-metadeta {
    flex: 1 1;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #121212;
    border-radius: 2rem;
}

.movie-overview {
    flex: 2 1;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.movie-overview h3 {
    margin: 0rem 1rem;
}

.movie-images {
    flex: 1 1;
    flex-direction: column;
    max-height: 90vh;
    overflow: scroll;
}

.movie-image {
    width: 80%;
    border-radius: 2rem;
    margin: 0;
}

.movie-poster {
    object-fit: contain;
    width: 150px;
    border-radius: 1rem;
    margin: 1rem 0rem;
}
